<template>
  <div class="the_body">
    <header class="app_bar">
      <a href="" class="logo">Les-nerds</a>
    </header>

    <main id="main">
      <div class="box" id="box">
        <h3 id="u_p"></h3>
        <hr />
        <h1
          style="font-size: 20px; font-weight: 900; color: #3a6cf4"
          v-if="Result === true"
        >
          The Sujet has been sent successfully
        </h1>
        <div class="question" id="question" v-if="Result === false"></div>
        <hr v-if="Result === false" />
        <textarea
          v-if="Result === false"
          class="production"
          required
          placeholder="Write here..."
          id="production"
        ></textarea>

        <button type="button" v-on:click="F_Button()" v-if="Result === false">
          <img src="../../assets/right_arrow.png" alt="Submit" />
        </button>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  mounted() {
    this.Start();
  },
  name: "ProductionView",
  data() {
    return {
      question: "",
      Result: false,
      production: "",
    };
  },
  methods: {
    Start: async function () {
      if (!navigator.onLine) {
        window.alert("Check Internet Connection!");
        window.location.href = "../main-page";
      }
      var type = localStorage.getItem("type");
      var grade = localStorage.getItem("grade");
      var code = localStorage.getItem("code");
      var name = localStorage.getItem("name");
      var quiz_name = localStorage.getItem("quiz_name");

      let request = await fetch(
        `https://arugatu.000webhostapp.com/index.php?func=MrsOpen&code=${code[0]}${code[1]}${code[2]}&grade=${grade}&quizname=${quiz_name}`
      );
      let MrsOpen = await request.text();

      let request2 = await fetch(
        `https://arugatu.000webhostapp.com/index.php?func=CanEnter&code=${code}&username=${name}&quiz=${quiz_name}`
      );
      let CanEnter = await request2.text();

      quiz_name = `${quiz_name}-${grade}`;
      let TorF = MrsOpen === "True" && CanEnter === "True";
      if (TorF === true && type === "p") {
        var request3 = await fetch("https://hazememad4.github.io/");
        var all_quizes_as_text = await request3.text();
        var data = JSON.parse(all_quizes_as_text);
        data = data[quiz_name];
        if (Object.keys(data).length === 0) {
          window.location.href = "/main-page";
        }
        this.Part1();
      } else {
        window.location.href = "/main-page";
      }
    },
    Part1: async function () {
      var request3 = await fetch("https://hazememad4.github.io/");
      var all_quizes_as_text = await request3.text();
      var data = JSON.parse(all_quizes_as_text);
      var grade = localStorage.getItem("grade");
      var quiz_name = localStorage.getItem("quiz_name");
      var n = localStorage.getItem("n");
      quiz_name = `${quiz_name}-${grade}`;
      data = data[quiz_name];
      document.getElementById(
        "u_p"
      ).innerHTML = `Unit ${quiz_name[4]} - Quiz ${n}`;
      document.getElementById("question").innerHTML = data["q"];
    },
    F_Button: async function () {
      this.production = document.getElementById("production").value;
      if (this.production.length === 0) {
        document.getElementById("production").value = "Write First!";
      } else {
        this.Check();
      }
    },
    Check: async function () {
      this.Result = true;
      if (!navigator.onLine) {
        window.alert(
          "You are disconnected! Check Internet Connection then press OK"
        );
      }
      if (!navigator.onLine) {
        window.location.href = "/main-page";
      }
      var code = localStorage.getItem("code");
      var name = localStorage.getItem("name");
      var quiz_name = localStorage.getItem("quiz_name");
      await fetch(
        `https://arugatu.000webhostapp.com/index.php?func=SolveSaas&id=${code[0]}${code[1]}${code[2]}&code=${code}&name=${name}&quizname=${quiz_name}&content=${this.production}`
      );
    },
  },
};
</script>

