<template>
  <div class="the_body">
    <header class="app_bar">
      <a class="logo">Les-nerds</a>
      <a class="log_out" href="/">Log out</a>
    </header>
    <main>
      <div class="content">
        <div class="up-f">
          <div>
            <h3 class="title" id="title"></h3>
          </div>

          <div>
            <span id="loading">Loading...</span>
          </div>
        </div>

        <a v-on:click="card3_clicked(1)">
          <div class="quiz_name">
            <h5>Quiz 1</h5>
          </div>
        </a>

        <a v-on:click="card3_clicked(2)">
          <div class="quiz_name">
            <h5>Quiz 2</h5>
          </div>
        </a>

        <a v-on:click="card3_clicked(3)">
          <div class="quiz_name">
            <h5>Quiz 3</h5>
          </div>
        </a>

        <a v-on:click="card3_clicked(4)">
          <div class="quiz_name">
            <h5>Quiz 4</h5>
          </div>
        </a>

        <a v-on:click="card3_clicked(5)">
          <div class="quiz_name">
            <h5>Quiz 5</h5>
          </div>
        </a>

        <a v-on:click="card3_clicked(6)">
          <div class="quiz_name">
            <h5>Quiz 6</h5>
          </div>
        </a>

        <a v-on:click="card3_clicked(7)">
          <div class="quiz_name">
            <h5>Quiz 7</h5>
          </div>
        </a>
        <a v-on:click="card3_clicked(8)">
          <div class="quiz_name">
            <h5>Quiz 8</h5>
          </div>
        </a>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  mounted() {
    var name = localStorage.getItem("unit_word");
    document.getElementById("title").innerHTML = name;
  },
  name: "QuizPage",
  methods: {
    card3_clicked: async function (n) {
      try {
        if (!navigator.onLine) {
          window.alert("Check Internet Connection!");
          return false;
        }
        document.getElementById("loading").style.display = "inline";
        var unit = localStorage.getItem("unit");
        var type = localStorage.getItem("type");
        var grade = localStorage.getItem("grade");
        var code = localStorage.getItem("code");
        var name = localStorage.getItem("name");
        var quiz_name = `unit${unit}${type}${n}`;
        localStorage.setItem("quiz_name", quiz_name);
        localStorage.setItem("n", n);

        let request = await fetch(
          `https://arugatu.000webhostapp.com/index.php?func=MrsOpen&code=${code[0]}${code[1]}${code[2]}&grade=${grade}&quizname=${quiz_name}`
        );
        let MrsOpen = await request.text();

        let request2 = await fetch(
          `https://arugatu.000webhostapp.com/index.php?func=CanEnter&code=${code}&username=${name}&quiz=${quiz_name}`
        );
        let CanEnter = await request2.text();
        document.getElementById("loading").style.display = "none";

        let TorF = MrsOpen === "True" && CanEnter === "True";
        if (TorF === true) {
          if (type === "g") {
            window.location.href = "/main-page/unit/quiz/grammair";
          }
          if (type === "s") {
            window.location.href = "/main-page/unit/quiz/situation";
          }
          if (type === "c") {
            window.location.href = "/main-page/unit/quiz/comprehension";
          }
          if (type === "e") {
            window.location.href = "/main-page/unit/quiz/email";
          }
          if (type === "p") {
            window.location.href = "/main-page/unit/quiz/production";
          }
        }
      } catch (e) {
        document.getElementById("loading").style.display = "none";
      }
    },
  },
};
</script>

