    Check: async function () {
      var CorrectedAns = 0;
      this.answers = 0;
      this.GorR = {};
      var grade = localStorage.getItem("grade");
      var quiz_name = localStorage.getItem("quiz_name");
      quiz_name = `${quiz_name}-${grade}`;
      var data = JSON.parse(this.data);
      data = data[quiz_name];
      var cans = data["cans"];
      for (var i = 1; i < 41; i++) {
        for (var x = 1; x < 5; x++) {
          if (
            document.getElementById(`ans${i}${x}_b`).style.display === "block"
          ) {
            this.answers++;
            this.student_r[i] = x;
          }
        }
      }
      if (this.answers === 40) {
        for (i = 1; i < 41; i++) {
          if (`${cans[i]}` === `${this.student_r[i]}`) {
            this.GorR[`${i}${cans[`${i}`]}`] = "GREEN";
            CorrectedAns++;
          } else {
            this.GorR[`${i}${this.student_r[`${i}`]}`] = "RED";
            this.GorR[`${i}${cans[`${i}`]}`] = "GREEN";
          }
        }
        this.Result = true;
        var Percentage = parseInt((CorrectedAns / 40) * 100);
        this.r_t = parseInt(Percentage);
        if (!navigator.onLine) {
          window.alert(
            "You are disconnected! Check Internet Connection then press OK"
          );
        }
        if (!navigator.onLine) {
          window.location.href = "/main-page";
        }
        var code = localStorage.getItem("code");
        var name = localStorage.getItem("name");
        quiz_name = localStorage.getItem("quiz_name");
        fetch(
          `https://arugatu.000webhostapp.com/index.php?func=SolveQuiz&code=${code}&name=${name}&quizname=${quiz_name}&dorl=${Percentage}`
        );
        this.Part2();
      }
    },
