import { createRouter, createWebHistory } from "vue-router";
import StudentLoginView from "../views/StudentLoginView.vue";
import MainPageView from "../views/MainPageView.vue";
import UnitPageView from "../views/UnitPageView.vue";
import QuizPageView from "../views/QuizPageView.vue";
import SignUpView from "../views/SignUpView.vue";
import GrammerView from "../views/Quizes/GrammerView.vue";
import SituationView from "../views/Quizes/SituationsView.vue";
import ComprehensionView from "../views/Quizes/ComprehensionView.vue";
import EmailView from "../views/Quizes/EmailView.vue";
import ProductionView from "../views/Quizes/ProductionView.vue";
import Eval3View from "../views/Quizes/Eval3View.vue";
import Exam3View from "../views/Quizes/Exam3View.vue";
import TOCView from "../views/Quizes/StemConceptsView.vue";
import URTView from "../views/Quizes/StemUniversityView.vue";

const routes = [
  {
    path: "/",
    name: "IndexView",
    component: StudentLoginView,
    meta: {
      title: "Les-nerds",
    },
  },
  {
    path: "/main-page",
    name: "mainpage",
    component: MainPageView,
    meta: {
      title: "Les-nerds",
    },
  },
  {
    path: "/main-page/choose",
    name: "unitpage",
    component: UnitPageView,
    meta: {
      title: "Les-nerds",
    },
  },
  {
    path: "/main-page/choose/quiz",
    name: "quizpage",
    component: QuizPageView,
    meta: {
      title: "Les-nerds",
    },
  },
  {
    path: "/signup",
    name: "signup",
    component: SignUpView,
    meta: {
      title: "Les-nerds",
    },
  },
  {
    path: "/main-page/unit/quiz/grammair",
    name: "grammer",
    component: GrammerView,
    meta: {
      title: "Les-nerds",
    },
  },
  {
    path: "/main-page/unit/quiz/situation",
    name: "situation",
    component: SituationView,
    meta: {
      title: "Les-nerds",
    },
  },
  {
    path: "/main-page/unit/quiz/comprehension",
    name: "comprehension",
    component: ComprehensionView,
    meta: {
      title: "Les-nerds",
    },
  },
  {
    path: "/main-page/unit/quiz/email",
    name: "email",
    component: EmailView,
    meta: {
      title: "Les-nerds",
    },
  },
  {
    path: "/main-page/unit/quiz/production",
    name: "production",
    component: ProductionView,
    meta: {
      title: "Les-nerds",
    },
  },
  {
    path: "/main-page/evaluation3",
    name: "eval3",
    component: Eval3View,
    meta: {
      title: "Les-nerds",
    },
  },
  {
    path: "/main-page/exam3",
    name: "exam3",
    component: Exam3View,
    meta: {
      title: "Les-nerds",
    },
  },
  {
    path: "/main-page/toc",
    name: "toc",
    component: TOCView,
    meta: {
      title: "Les-nerds",
    },
  },
  {
    path: "/main-page/urt",
    name: "urt",
    component: URTView,
    meta: {
      title: "Les-nerds",
    },
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach((to) => {
  document.title = to.meta?.title ?? "Title";
});

export default router;
