<template>
  <div class="the_body">
    <div class="main1">
      <table>
        <tr>
          <td>
            <div class="the_form">
              <h3 class="title" id="title">Log In</h3>
              <p id="fail"></p>
              <label for="username" class="username_label">Username</label>
              <input
                type="text"
                id="username"
                class="username"
                autocomplete="false"
              />
              <p id="username_error"></p>
              <label for="code" class="code_label">Code</label>
              <input type="text" id="code" class="code" autocomplete="false" />
              <p id="code_error"></p>
              <button
                type="submit"
                value="Submit"
                class="submit_button"
                v-on:click="login"
              >
                Log In
              </button>
              <span class="final_text">Don't have an account? </span>
              <a href="/signup" class="login_link">Sign Up</a>
            </div>
          </td>
          <td>
            <div class="image">
              <img src="../assets/main_log.png" alt="Image" />
            </div>
          </td>
        </tr>
      </table>
    </div>
  </div>
  <div class="popup_loader" id="popup_loader">
    <div class="loader" id="loader"></div>
  </div>
  <div class="overlay" id="overlay"></div>
</template>

<script>
export default {
  mounted() {
    this.Rtli();
  },
  methods: {
    login: function () {
      if (!navigator.onLine) {
        window.alert("Check Internet Connection!");
        return false;
      }

      document.getElementById("overlay").style.display = "block";
      document.getElementById("popup_loader").style.display = "flex";
      var x = true;
      var username = document.getElementById("username").value;
      var code = document.getElementById("code").value;
      var xrr = this.OL();

      document.getElementById("code_error").innerHTML = "";
      document.getElementById("code").style = "margin-bottom: 15px";
      document.getElementById("username_error").innerHTML = "";
      document.getElementById("fail").style.display = "none";
      document.getElementById("title").style.margin = "0px 0px 10px 0px";

      if (xrr === false) {
        document.getElementById("username_error").innerHTML =
          "Username musn't be more than 20";
        document.getElementById("username").style.marginBottom = "0px";
        x = false;
      }

      if (code.length === 0) {
        document.getElementById("code_error").innerHTML = "Code is required!";
        document.getElementById("code").style = "margin-bottom: 0px";
        document.getElementById("code_error").style = "height: 1px;";
        document.getElementById("code_error").style = "font-size: 1px;";
        document.getElementById("code_error").style = "color: rgb(255, 0, 0)";
        x = false;
      }
      if (username.length === 0) {
        document.getElementById("username_error").innerHTML =
          "Username is required!";
        document.getElementById("username").style.marginBottom = "0px";
        x = false;
      }
      if (x === true) {
        try {
          var request = new XMLHttpRequest();
          request.open(
            "GET",
            `https://arugatu.000webhostapp.com/index.php?func=SignIn&code=${code}&name=${username}`
          );
          request.send();
          request.onreadystatechange = async function () {
            if (this.readyState === 4 && this.status === 200) {
              document.getElementById("popup_loader").style.display = "none";
              document.getElementById("overlay").style.display = "none";

              if (this.responseText === "Failed") {
                document.getElementById("title").style.margin =
                  "0px 0px 0px 0px";
                document.getElementById("fail").innerHTML = "Invalid Data";
                document.getElementById("fail").style.display = "block";
              }
              if (this.responseText === "Success") {
                try {
                  localStorage.setItem("grade", code[code.length - 1]);
                  localStorage.setItem("code", code);
                  localStorage.setItem("name", username);
                  let request2 = await fetch(
                    `https://arugatu.000webhostapp.com/index.php?func=MrsData&code=${code[0]}${code[1]}${code[2]}`
                  );
                  let teacher = await request2.text();
                  localStorage.setItem("teacher", teacher);
                  window.location.href = "/main-page";
                } catch (e) {
                  window.alert("Check Internet Connection!");
                  return false;
                }
              }
            }
          };
        } catch (e) {
          console.log("Error");
        }
      }
    },
    Rtli: async function () {
      try {
        var name = await localStorage.getItem("name");
        var code = await localStorage.getItem("code");
        if (code != null) {
          document.getElementById("code").value = code;
        }
        if (name != null) {
          document.getElementById("username").value = name;
        }
      } catch (e) {
        console.log("Error");
      }
    },
    OL: function (str) {
      return /^[A-Za-z]*$/.test(str);
    },
  },
  name: "StudentLogin",
};
</script>

