<template>
  <div class="the_body" v-if="Result === false">
    <header class="app_bar">
      <a href="" class="logo">Les-nerds</a>
    </header>

    <main id="main">
      <div class="box">
        <span id="u_p"></span>
        <h3 id="title">Question 0/0</h3>
        <hr />

        <div class="comp" id="comp"></div>
        <hr />
        <div class="question" id="question"></div>

        <a class="card" v-on:click="Ans_Clicked(1)">
          <div class="answer">
            <div>
              <span id="ans1"></span>
            </div>
            <div>
              <div id="ans1_b"></div>
            </div>
          </div>
        </a>

        <a class="card" v-on:click="Ans_Clicked(2)">
          <div class="answer">
            <div>
              <span id="ans2"></span>
            </div>
            <div>
              <div id="ans2_b"></div>
            </div>
          </div>
        </a>

        <a class="card" v-on:click="Ans_Clicked(3)">
          <div class="answer">
            <div>
              <span id="ans3"></span>
            </div>
            <div>
              <div id="ans3_b"></div>
            </div>
          </div>
        </a>

        <a class="card" v-on:click="Ans_Clicked(4)">
          <div class="answer">
            <div>
              <span id="ans4"></span>
            </div>
            <div>
              <div id="ans4_b"></div>
            </div>
          </div>
        </a>

        <div class="buttons">
          <button type="button" v-on:click="B_Button()">
            <img src="../../assets/left_arrow.png" alt="Back" />
          </button>
          <button type="button" v-on:click="F_Button()">
            <img src="../../assets/right_arrow.png" alt="Forward" />
          </button>
        </div>
      </div>
    </main>
  </div>
  <div class="the_body" v-if="Result === true">
    <header class="app_bar">
      <a href="" class="logo">Les-nerds</a>
    </header>
    <div class="box">
      <span id="u_p">Unit {{ unit }} - Quiz {{ quiz }}</span>

      <h3 id="title">Question {{ qanoq }}</h3>
      <hr />
      <div class="comp" id="comp" v-html="comp"></div>
      <div class="question" id="question">{{ question }}</div>

      <div
        class="answer"
        id="answer1"
        v-bind:style="{ backgroundColor: ans1back }"
      >
        <div>
          <span id="ans1">{{ ans1 }}</span>
        </div>
        <div id="x1" class="x" v-bind:style="{ display: x1back }">x</div>
      </div>

      <div
        class="answer"
        id="answer2"
        v-bind:style="{ backgroundColor: ans2back }"
      >
        <div>
          <span id="ans2">{{ ans2 }}</span>
        </div>
        <div id="x2" class="x" v-bind:style="{ display: x2back }">x</div>
      </div>

      <div
        class="answer"
        id="answer3"
        v-bind:style="{ backgroundColor: ans3back }"
      >
        <div>
          <span id="ans3">{{ ans3 }}</span>
        </div>
        <div id="x3" class="x" v-bind:style="{ display: x3back }">x</div>
      </div>

      <div
        class="answer"
        id="answer4"
        v-bind:style="{ backgroundColor: ans4back }"
      >
        <div>
          <span id="ans4">{{ ans4 }}</span>
        </div>
        <div id="x4" class="x" v-bind:style="{ display: x4back }">x</div>
      </div>

      <div class="result" id="r_t">{{ r_t }}</div>

      <div class="buttons">
        <button type="button" v-on:click="B_Button2()">
          <img src="../../assets/left_arrow.png" alt="Back" />
        </button>
        <button type="button" v-on:click="F_Button2()">
          <img src="../../assets/right_arrow.png" alt="Forward" />
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  mounted() {
    this.Start();
  },
  name: "ComprehensionView",
  data() {
    return {
      q: 1,
      noq: 0,
      student_r: {},
      GorR: {},
      CorrectedAnswers: 0,
      Percentage: 0,
      Result: false,
      comp: "",
      ans1back: "white",
      ans2back: "white",
      ans3back: "white",
      ans4back: "white",
      x1back: "none",
      x2back: "none",
      x3back: "none",
      x4back: "none",
      unit: "",
      quiz: "",
      qanoq: "",
      ans1: "",
      ans2: "",
      ans3: "",
      ans4: "",
      question: "",
      r_t: "",
    };
  },
  methods: {
    Start: async function () {
      if (!navigator.onLine) {
        window.alert("Check Internet Connection!");
        window.location.href = "/main-page";
      }
      var unit = localStorage.getItem("unit");
      var type = localStorage.getItem("type");
      var grade = localStorage.getItem("grade");
      var code = localStorage.getItem("code");
      var name = localStorage.getItem("name");
      var n = localStorage.getItem("n");
      var quiz_name = localStorage.getItem("quiz_name");

      let request = await fetch(
        `https://arugatu.000webhostapp.com/index.php?func=MrsOpen&code=${code[0]}${code[1]}${code[2]}&grade=${grade}&quizname=${quiz_name}`
      );
      let MrsOpen = await request.text();

      let request2 = await fetch(
        `https://arugatu.000webhostapp.com/index.php?func=CanEnter&code=${code}&username=${name}&quiz=${quiz_name}`
      );
      let CanEnter = await request2.text();

      quiz_name = `${quiz_name}-${grade}`;
      let TorF = MrsOpen === "True" && CanEnter === "True";
      if (TorF === true && type === "c") {
        var request3 = await fetch("https://hazememad4.github.io/");
        var all_quizes_as_text = await request3.text();
        var data = JSON.parse(all_quizes_as_text);
        data = data[quiz_name];

        document.getElementById("u_p").innerHTML = `Unit ${unit} - Quiz ${n}`;
        if (Object.keys(data).length === 0) {
          window.location.href = "/main-page";
        }
        this.Part1();
      } else {
        window.location.href = "/main-page";
      }
    },
    Part1: async function () {
      var request3 = await fetch("https://hazememad4.github.io/");
      var all_quizes_as_text = await request3.text();
      var quiz_name = localStorage.getItem("quiz_name");
      var data = JSON.parse(all_quizes_as_text);
      localStorage.setItem("data", all_quizes_as_text);
      var grade = localStorage.getItem("grade");
      quiz_name = `${quiz_name}-${grade}`;
      data = data[quiz_name];

      this.noq = data["noq"];

      document.getElementById("comp").innerHTML = data["comp"];
      document.getElementById(
        "title"
      ).innerHTML = `Question ${this.q}/${this.noq}`;
      document.getElementById("question").innerHTML = data["q"][`q${this.q}`];
      document.getElementById("ans1").innerHTML = data["ans"][`ans${this.q}1`];
      document.getElementById("ans2").innerHTML = data["ans"][`ans${this.q}2`];
      document.getElementById("ans3").innerHTML = data["ans"][`ans${this.q}3`];
      document.getElementById("ans4").innerHTML = data["ans"][`ans${this.q}4`];
    },
    Ans_Clicked: function (n) {
      document.getElementById(`ans1_b`).style.display = "none";
      document.getElementById(`ans2_b`).style.display = "none";
      document.getElementById(`ans3_b`).style.display = "none";
      document.getElementById(`ans4_b`).style.display = "none";
      document.getElementById(`ans${n}_b`).style.display = "block";
    },
    F_Button: async function () {
      var i;
      var x;
      var y =
        document.getElementById(`ans1_b`).style.display === "block" ||
        document.getElementById(`ans2_b`).style.display === "block" ||
        document.getElementById(`ans3_b`).style.display === "block" ||
        document.getElementById(`ans4_b`).style.display === "block";
      if (this.q === this.noq && y === true) {
        for (i = 1; i < 5; i++) {
          if (document.getElementById(`ans${i}_b`).style.display === "block") {
            x = i;
          }
          document.getElementById(`ans${i}_b`).style = "display: none";
        }
        this.student_r[this.q] = `${x}`;
        await this.Check();
        this.q = 1;
        this.Part2();
        return false;
      } else if (this.q - 1 != this.noq && y === true) {
        for (i = 1; i < 5; i++) {
          if (document.getElementById(`ans${i}_b`).style.display === "block") {
            x = i;
          }
          document.getElementById(`ans${i}_b`).style = "display: none";
        }
        this.student_r[this.q] = `${x}`;
        this.q++;
        this.Part1();
      }

      if (this.student_r[this.q] != undefined) {
        document.getElementById(
          `ans${this.student_r[this.q]}_b`
        ).style.display = "block";
      }
    },
    B_Button: function () {
      if (this.q != 1) {
        this.q--;
        for (var i = 1; i < 5; i++) {
          document.getElementById(`ans${i}_b`).style = "display: none";
        }
        document.getElementById(`ans${this.student_r[this.q]}_b`).style =
          "display: block";
        this.Part1();
      }
    },
    Check: async function () {
      var data = localStorage.getItem("data");
      data = JSON.parse(data);
      var grade = localStorage.getItem("grade");
      var quiz_name = localStorage.getItem("quiz_name");
      quiz_name = `${quiz_name}-${grade}`;
      data = data[quiz_name];
      data = data["cans"];

      if (data != undefined) {
        for (var i = 1; i < this.noq + 1; i++) {
          if (`${data[`${i}`]}` === `${this.student_r[`${i}`]}`) {
            this.CorrectedAnswers++;
            this.GorR[`${i}${this.student_r[`${i}`]}`] = "GREEN";
          } else {
            this.GorR[`${i}${this.student_r[`${i}`]}`] = "RED";
            this.GorR[`${i}${data[`${i}`]}`] = "GREEN";
          }
        }
        this.Percentage = parseInt((this.CorrectedAnswers / this.noq) * 100);
      }
      if (!navigator.onLine) {
        window.alert(
          "You are disconnected! Check Internet Connection then press OK"
        );
      }
      if (!navigator.onLine) {
        window.location.href = "/main-page";
      }
      var code = localStorage.getItem("code");
      var name = localStorage.getItem("name");
      quiz_name = localStorage.getItem("quiz_name");
      await fetch(
        `https://arugatu.000webhostapp.com/index.php?func=SolveQuiz&code=${code}&name=${name}&quizname=${quiz_name}&dorl=${this.Percentage}`
      );
    },
    Part2: function () {
      this.Result = true;
      var data = localStorage.getItem("data");
      var unit = localStorage.getItem("unit");
      var n = localStorage.getItem("n");
      data = JSON.parse(data);
      var grade = localStorage.getItem("grade");
      var quiz_name = localStorage.getItem("quiz_name");
      quiz_name = `${quiz_name}-${grade}`;
      data = data[quiz_name];
      this.comp = data["comp"];
      this.unit = unit;
      this.quiz = n;
      this.question = data["q"][`q${this.q}`];
      this.ans1 = data["ans"][`ans${this.q}1`];
      this.ans2 = data["ans"][`ans${this.q}2`];
      this.ans3 = data["ans"][`ans${this.q}3`];
      this.ans4 = data["ans"][`ans${this.q}4`];
      this.qanoq = `${this.q}/${this.noq}`;
      this.r_t = `${this.Percentage} %`;
      this.Check2();
    },
    Check2: function () {
      this.ans1back = "white";
      this.ans2back = "white";
      this.ans3back = "white";
      this.ans4back = "white";

      this.x1back = "none";
      this.x2back = "none";
      this.x3back = "none";
      this.x4back = "none";

      if (this.GorR[`${this.q}1`] === "GREEN") {
        this.ans1back = "rgba(21, 255, 0, 0.21)";
      }
      if (this.GorR[`${this.q}2`] === "GREEN") {
        this.ans2back = "rgba(21, 255, 0, 0.21)";
      }
      if (this.GorR[`${this.q}3`] === "GREEN") {
        this.ans3back = "rgba(21, 255, 0, 0.21)";
      }
      if (this.GorR[`${this.q}4`] === "GREEN") {
        this.ans4back = "rgba(21, 255, 0, 0.21)";
      }

      if (this.GorR[`${this.q}1`] === "RED") {
        this.x1back = "block";
      }
      if (this.GorR[`${this.q}2`] === "RED") {
        this.x2back = "block";
      }
      if (this.GorR[`${this.q}3`] === "RED") {
        this.x3back = "block";
      }
      if (this.GorR[`${this.q}4`] === "RED") {
        this.x4back = "block";
      }
    },
    F_Button2: function () {
      if (this.q != this.noq) {
        this.q++;
        this.Part2();
      }
    },
    B_Button2: function () {
      if (this.q != 1) {
        this.q--;
        this.Part2();
      }
    },
  },
};
</script>

